body {
  min-height: 100vh;
  height: 100%;
  width: 100vw;
  padding: 0;
  margin: 0;
}

.App {
  min-height: 100vh;
  height: 100%;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  background: black;
  color: chartreuse;
  font-family: "Comfortaa";
}

p {
  padding: 0;
  margin: 0;
  -webkit-animation: fadeIn 0.5s linear 1s;
          animation: fadeIn 0.5s linear 1s;
}

span {
  -webkit-animation: fadeIn 1s linear 1s;
          animation: fadeIn 1s linear 1s;
}

h1 {
  text-align: center;
  width: 100%;
}

.flash {
  -webkit-animation: flash .25s linear .25s;
          animation: flash .25s linear .25s;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes slideIn {
  from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }

  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes slideIn {
  from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }

  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@-webkit-keyframes flash {
  0% {
    text-shadow: none;
  }
  100% {
    text-shadow: 0px 0px 50px chartreuse;
  }
}

@keyframes flash {
  0% {
    text-shadow: none;
  }
  100% {
    text-shadow: 0px 0px 50px chartreuse;
  }
}
@media only screen and (max-width: 768px) {
  body {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
  }
  .App {
    height: 100%;
    width: 100%;
    color: white;
  }
  .label {
    display: none;
    visibility: hidden;
  }
}

